@import 'slick';
@import 'slick-theme';
@import 'variables-slider';
@import 'mixin-slider';

// === slider 2 ===

.characteristics-slider-box {
    width: 43%;

    .characteristics-slider {
        margin-top: 40px;

        &__item {
            position: relative;
            background: #fff;
        }

        &__img {
            width: 67%;
            padding-top: 60px;
            margin: 0 auto;
        }

        &__price {
            position: absolute;
            top: 0;
            right: 0;
            left: 0;
            box-sizing: border-box;
            display: flex;
            align-items: center;
            justify-content: space-between;
            width: 100%;
            color: $color-white;
            background: $bg-blue-sl;
        }

        &__text {
            padding: 15px;
            margin: 0;
            font-size: $fz-text-22px;
            font-weight: 700;

            span:first-child {
                font-weight: 400;
            }

            span:last-child {
                margin-left: 10px;
                font-size: $fz-text-30px;
            }
        }

        &__discount {
            position: relative;
            padding: 15px 15px 15px 5px;
            font-size: $fz-text-30px;
            font-weight: 700;
            background: $bg-yellow;

            ::before {
                position: absolute;
                top: 0;
                bottom: 0;
                left: -50px;
                content: '';
                border: 30px solid transparent;
                border-right: 20px solid $bg-yellow;
            }
        }

        // === control ===

        .slick-next {
            right: $retreat-right-sl;
        }

        .slick-prev {
            left: $retreat-left-sl;
            transform: matrix(1, 0, 0, 1, 0, -20) rotate(180deg);
        }

        .slick-next,
        .slick-prev {
            @include arrow;
        }

        &.slick-active {
            button {
                border: $border-dots-hover-sl;

                &::before {
                    background: $bg-dots-hover-sl;
                }
            }
        }
    }

    .slider-nav {
        display: flex;
        justify-content: space-between;
        margin: 5px auto 0;

        &__item {
            position: relative;
            flex-basis: 20%;
            max-width: 71px;
            max-height: 71px;
            padding: 5px;
        }

        .slick-slide {
            max-height: 100px;
            margin: 0 2px;
        }

        img {
            width: 100%;
        }
    }
}

