@mixin arrow {
    z-index: 2;
    width: $width-arrow-sl;
    height: $height-arrow-sl;
    background: url(../fonts/slider/arrow.svg);
    background-size: 100%;
    opacity: $opacity-arrow-sl;
    transition: opacity $transition-speed-sl;

    &:hover,
    &:focus {
        opacity: $opacity-arrow-hover-sl;
    }

    &::before {
        content: ' ';
    }
}
