@import 'normalize/normalize.min';
@import 'fonts';
@import 'variables';
@import 'mixin';
@import 'slider/style-slider-1';

body {
    font-family: 'Helvetica Neue', sans-serif;
    font-size: $fz-text-18px;
    color: $color-gray-dark;
}

.header,
.footer {
    .ordering-info-1 {
        background: $bg-white;

        &__body {
            @include body;

            display: flex;
            justify-content: space-between;
        }

        &__logo {
            padding-top: 5px;
            margin-right: 20px;

            svg {
                width: 82px;
                height: 80px;
            }
        }

        &__title {
            @include title-basis;

            flex-grow: 1;
            font-weight: 400;

            span {
                display: block;
                width: 100%;
            }
        }

        &__phone {
            display: flex;
            flex-basis: 35%;
            flex-direction: column;
            align-items: flex-end;
            min-width: 255px;
            padding-top: 10px;
            margin-left: 20px;
        }

        &__phone-number {
            display: flex;
            align-items: center;
            height: 30px;

            a {
                font-size: $fz-text-30px;
                color: $color-gray-dark;
            }

            svg {
                width: 18px;
                height: 30px;
                margin-right: 10px;
                fill: $color-green;
            }
        }

        .callback {
            position: relative;
            width: 100%;
            max-width: 250px;
            margin-top: 15px;

            &__title,
            &__text {
                text-align: center;

                span {
                    font-size: $fz-text-22px;
                }
            }

            &__button-box {
                transition: all $transition-mw;
            }

            &__button {
                @include btn-basis(24px);

                display: flex;
                align-items: center;
                justify-content: center;
                margin: 0 auto;
                text-align: center;
                cursor: pointer;
                background-color: $bg-green;
                transition: background $transition-mw;

                &:hover {
                    background: $bg-blue;
                }

                span {
                    padding: 5px 0;
                    font-size: $fz-text-22px;
                    color: $color-white;
                }
            }

            &__overlay {
                position: fixed;
                top: 0;
                left: 0;
                z-index: $z-index-mw-overlay;
                display: none;
                width: $width-mw-overlay;
                height: $height-mw-overlay;
                background-color: $bg-mw-overlay;
            }

            &__popup {
                position: absolute;
                top: 50%;
                left: 50%;
                box-sizing: border-box;
                width: $width-mw-popup;
                padding: $padding-mw-popup;
                color: $color-mw-text;
                background-color: $bg-red;
                border-radius: $br-mw-item;
                transform: translate(-50%, -50%);

                form {
                    display: flex;
                    flex-direction: column;
                    align-items: center;
                    justify-content: center;
                }
            }

            &__btn-send {
                @include trans-basis;

                width: 70%;
                padding: 10px;
                margin-bottom: 20px;
                color: $color-mw-text;
                cursor: pointer;
                background: $bg-mw-button;
                border: 2px solid transparent;
                border-radius: 5px;
                outline: none;

                &:hover {
                    background: $bg-yellow;
                }
            }

            &__input-name,
            &__input-tel {
                @include trans-basis;

                width: 70%;
                padding: 10px;
                margin-bottom: 20px;
                border: 2px solid transparent;
                border-radius: 5px;
                outline: none;

                &:focus {
                    border: 2px solid $color-blue;
                }
            }

            &__close-popup {
                position: absolute;
                top: 15px;
                right: 15px;
                width: $width-mw-close-popup;
                height: $height-mw-close-popup;
                cursor: pointer;

                &::before {
                    position: absolute;
                    top: 11px;
                    left: -4px;
                    width: $width-mw-close-popup-bef-af;
                    height: $height-mw-close-popup-bef-af;
                    content: '';
                    background-color: $bg-mw-close-popup;
                    transform: rotate(-45deg);
                }

                &::after {
                    position: absolute;
                    top: 11px;
                    left: -4px;
                    width: $width-mw-close-popup-bef-af;
                    height: $height-mw-close-popup-bef-af;
                    content: '';
                    background-color: $bg-mw-close-popup;
                    transform: rotate(45deg);
                }
            }
        }
    }

    .ordering-info-2 {
        background: url(../img/bg-basis.jpg);

        &__body {
            @include body;

            display: flex;
            justify-content: space-between;
            padding-top: 10px;
        }

        &__img-box {
            position: relative;
            display: flex;
            align-items: center;

            img {
                width: 100%;
            }
        }

        &__price {
            position: absolute;
            top: 0;
            right: 0;
            left: 175px;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            max-width: 125px;
            min-height: 125px;
            text-align: center;
            background: $bg-yellow;
            border-radius: 50%;

            p {
                margin: 0;
                font-size: $fz-text-22px;
            }

            b {
                margin-top: 5px;
                font-size: $fz-text-30px;
                color: $color-red;
            }
        }

        &__feature {
            position: absolute;
            bottom: 30px;
            display: flex;
            align-items: center;

            div {
                display: flex;
                flex-direction: column;
                margin: 0 0 5px 20px;
            }

            svg {
                width: 29px;
                height: 28px;
            }
        }

        &__content-box {
            flex-basis: 37%;
        }

        .ordering-info-2-form {
            box-sizing: border-box;
            display: flex;
            flex-direction: column;
            align-items: flex-end;
            min-width: 320px;
            margin-top: 10px;
            background: $bg-white;

            &__title {
                display: flex;
                flex-direction: column;
                width: 100%;
                padding: 5px 0;
                text-align: center;
                background: $bg-yellow-with-transparency;

                h3 {
                    @include title-basis($fz-title-24px, $color-white);

                    margin: 0;
                    text-transform: uppercase;
                }

                span {
                    @include text-basis ($fz-text-18px, $color-white);
                }
            }

            &__input {
                padding: 0 10px;
                margin-top: 10px;

                input {
                    box-sizing: border-box;
                    width: 100%;
                    padding: 10px 15px;
                    margin-bottom: 10px;
                    font-style: italic;
                    background: $bg-gray-light;
                    border: none;
                }
            }

            &__checkbox {
                box-sizing: border-box;
                display: flex;
                flex-direction: column;
                width: 100%;
                padding: 5px 10px;
                font-size: $fz-text-14px;

                div {
                    display: flex;
                }

                div:nth-child(2) {
                    margin-top: 10px;
                }

                span {
                    color: $color-red;
                }

                label {
                    flex-basis: 87%;
                    padding-left: 15px;
                }

                svg {
                    width: 20px;
                    height: 20px;
                    cursor: pointer;

                    .a {
                        @include trans-basis;

                        fill: $color-yellow;

                        &:hover {
                            fill: $color-green;
                        }
                    }
                }

                #check1,
                #check2,
                #check3,
                #check4 {
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    width: 18px;
                    height: 18px;
                    cursor: pointer;
                    background: $bg-gray-light;
                    border: 1px solid rgba(51, 51, 51, 0.2);
                }

                .check-active-red {
                    position: relative;

                    &::before {
                        position: absolute;
                        top: 0;
                        right: 0;
                        bottom: 0;
                        left: 0;
                        width: 14px;
                        height: 14px;
                        margin: auto;
                        content: '';
                        background: $bg-red;
                    }
                }

                input {
                    display: none;
                }
            }

            &__btn {
                box-sizing: border-box;
                width: 100%;
                padding: 0 10px;
                margin-top: 5px;
                cursor: pointer;

                button {
                    @include trans-basis(background);

                    width: 100%;
                    padding: 15px 0;
                    font-size: $fz-text-24px;
                    color: $color-white;
                    cursor: pointer;
                    background: $bg-red-transparency;
                    border: none;
                    outline: none;

                    &:hover {
                        background: $bg-blue;
                    }
                }
            }
        }

        .ordering-info-2-counter {
            box-sizing: border-box;
            display: flex;
            width: 100%;
            margin-top: 30px;
            background: $bg-white;

            &__stock {
                @include triangle-top;

                flex-basis: 30%;
                padding-top: 15px;
                text-align: center;
                background: $bg-yellow-with-transparency;

                b {
                    letter-spacing: 1.1px;
                }

                p {
                    padding: 0;
                    margin: 5px 0 0 0;
                }

                span {
                    display: block;
                    padding-bottom: 15px;
                    font-size: $fz-text-24px;
                    font-weight: 600;
                    color: $color-red;
                }
            }

            &__container {
                display: flex;
                flex-direction: column;
                padding: 10px 0 0 15px;

                h4 {
                    margin: 0;
                }
            }

            &__list {
                display: flex;
                justify-content: space-between;
                padding: 0;
                margin: 5px 0 0 0;
                list-style: none;

                li {
                    display: flex;
                    flex-direction: column;
                    padding: 0;
                    font-size: $fz-text-36px;
                    color: $color-red;
                    text-align: center;

                    p {
                        margin: 0;
                    }

                    span {
                        @include text-basis($fz-text-14px);
                    }
                }
            }
        }
    }
}

.info {
    background: url('../img/bg-basis-2.jpg');

    &__body {
        @include body;
    }

    .info-box {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        padding-top: 25px;

        &__item {
            @include section-info;

            flex-basis: 33%;
            max-height: 150px;
            padding-bottom: 35px;
            text-align: center;
        }
    }
}

.characteristics {
    background: url(../img/bg-basis.jpg);

    &__body {
        @include body;
    }

    .characteristics-box {
        &__title {
            @include title-basis(36px);

            padding-top: 45px;
            margin: 0;
            text-align: center;
        }

        &__content {
            display: flex;
            flex-wrap: wrap;
            justify-content: space-between;
        }

        .characteristics-slider-box {
            flex-basis: 40%;
            min-width: 300px;
        }

        .characteristics-text-info {
            flex-basis: 50%;
            min-width: 300px;
            padding: 35px 0 40px 0;

            h3 {
                @include title-basis;

                margin: 0;
            }

            ul {
                padding: 0;
                list-style: none;

                li {
                    padding: 0;
                    line-height: 1.3;
                }
            }
        }
    }
}

.characteristics-footer {
    @include triangle-top;

    background: $bg-yellow-with-transparency;

    &__body {
        @include body;
    }

    &__form {
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 20px 0;
    }

    &__item {
        box-sizing: border-box;
        flex-grow: 1;
        max-width: 220px;
        max-height: 40px;
        padding: 10px;
        border: none;

        &::placeholder {
            font-style: italic;
        }
    }

    &__btn {
        @include title-basis(24px, $color-white);
        @include trans-basis(background);

        flex-basis: 23%;
        padding: 20px 0;
        text-transform: uppercase;
        background: $bg-red-transparency;
        border: none;

        &:hover {
            background: $bg-blue;
        }
    }
}

.description {
    padding-bottom: 25px;
    background: url('../img/bg-basis-2.jpg');

    &__body {
        @include body;
    }

    .description-box {
        &__title {
            @include title-basis($fz-title-36px, $color-white);

            padding: 40px 0;
            margin: 0;
            text-align: center;
        }

        &__text {
            @include text-basis($fz-text-18px, $color-white);

            padding-bottom: 30px;
            margin: 0;
            line-height: 1.2;
        }
    }
}

.related-products {
    padding-bottom: 10px;
    background: url('../img/bg-basis.jpg');

    &__body {
        @include body;
    }

    .related-products-box {
        &__title {
            @include title-basis($fz-title-36px);

            padding: 50px 0;
            margin: 0;
            text-align: center;
        }

        &__item {
            display: flex;
            margin-bottom: 20px;
        }

        &__text {
            padding-left: 20px;
            margin: 0;
            line-height: 1.2;
        }

        &__img {
            position: relative;
        }

        &__info {
            position: absolute;
            top: 0;
            right: 0;
            left: 0;
            padding: 10px 0;
            margin: 0;
            color: $color-white;
            text-align: center;
            background: $bg-gray-dark;
        }
    }
}

.video {
    padding-bottom: 50px;
    background: url('../img/bg-basis-3.jpg');

    &__body {
        @include body;
    }

    &__title {
        @include title-basis($fz-title-36px, $color-white);

        padding: 40px 0;
        margin: 0;
        text-align: center;
    }

    .video-box {
        display: flex;
        justify-content: space-between;

        &__item {
            display: flex;
            flex-basis: 50%;
            flex-direction: column;
        }

        &__content {
            flex-grow: 1;
            width: 100%;

            iframe {
                width: 100%;
                height: 100%;
                max-height: 290px;
            }
        }

        &__title-2 {
            margin: 15px 0;
            font-size: $fz-text-18px;
            font-weight: 400;
            color: $color-white;
        }
    }
}

.reviews {
    padding-bottom: 25px;
    background: url('../img/bg-basis.jpg');

    &__body {
        @include body;
    }

    &__title {
        @include title-basis($fz-title-36px);

        padding: 45px 0;
        margin: 0;
        text-align: center;
    }

    .reviews-box {
        &__item {
            display: flex;
            margin-bottom: 35px;

            &:last-child,
            &:first-child {
                .reviews-box__text {
                    margin: 0 0 0 20px;
                    font-style: italic;
                    line-height: 1.2;
                }
            }

            &:nth-child(2) {
                margin-bottom: 30px;

                .reviews-box__text {
                    margin: 0 20px 0 0;
                    font-style: italic;
                    line-height: 1.2;
                }
            }

            img {
                max-height: 130px;
            }
        }

        &__nickname {
            float: right;
            margin-top: 20px;
            font-style: normal;
            color: $color-blue;
        }
    }
}

.results {
    background: url('../img/bg-basis-2.jpg');

    &__body {
        @include body;
    }

    &__title {
        @include title-basis($fz-title-36px, $color-white);

        padding: 45px 0;
        margin: 0;
        text-align: center;
    }

    .results-box {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        max-width: 770px;
        margin: 0 auto;

        &__item {
            position: relative;
            display: flex;
            flex-basis: 43%;
            justify-content: center;
            min-width: 360px;
            margin-bottom: 55px;

            img {
                width: 100%;
            }
        }

        &__info {
            position: absolute;
            right: 0;
            bottom: 0;
            left: 0;
            display: flex;
            justify-content: space-between;
            text-align: center;
        }

        &__info-text {
            flex-basis: 50%;
            font-size: $fz-text-24px;

            &:first-child {
                padding: 10px 0;
                color: $color-yellow;
                background: $bg-gray-dark;
            }

            &:last-child {
                padding: 10px 0;
                color: $color-gray-dark;
                background: $bg-yellow;
            }
        }
    }
}

// === media ===

@media screen and (max-width: 990px) {
    .characteristics {
        .characteristics-box {
            .characteristics-slider-box {
                max-width: 600px;
                margin: 0 auto;

                .characteristics-slider {
                    &__price {
                        div:nth-child(1) {
                            margin: 0 auto;
                        }
                    }
                }
            }

            .characteristics-text-info,
            .characteristics-slider-box {
                flex-basis: 100%;
                align-self: center;
                text-align: center;
            }
        }
    }

    .characteristics-footer {
        &__form {
            flex-wrap: wrap;
            justify-content: center;
        }

        &__item {
            margin: 20px 10px;
        }
    }
}

@media screen and (max-width: 830px) {
    .header,
    .footer {
        .ordering-info-1 {
            &__title {
                align-self: center;
                font-size: 18px;
            }
        }
    }

    .info {
        .info-box {
            flex-wrap: wrap;
            justify-content: space-around;

            &__item {
                min-width: 250px;
                padding-top: 20px;

                h3 {
                    font-size: 24px;
                }

                p {
                    font-size: 24px;
                }
            }
        }
    }

    .video {
        .video-box {
            justify-content: center;

            &__img {
                display: none;
            }

            &__item {
                flex-basis: 80%;
            }

            &__content {
                height: 250px;
                text-align: center;

                &:last-child {
                    margin: 70px 0;
                }
            }
        }
    }

    .results {
        .results-box {
            justify-content: center;

            &__item {
                margin: 0 10px 55px 10px;
            }
        }
    }
}

@media screen and (max-width: 730px) {
    .header,
    .footer {
        .ordering-info-1 {
            justify-content: space-between;

            &__logo {
                flex-grow: 1;
            }

            &__title {
                align-self: center;
                font-size: 18px;
            }

            &__phone-number {
                justify-content: center;
            }

            .header-phone {
                display: none;
            }

            .footer-title {
                font-size: 15px;
            }
        }

        .ordering-info-2 {
            &__body {
                flex-wrap: wrap;
                justify-content: center;
            }

            &__content-box {
                margin: 30px 0;
            }

            .callback {
                &__popup {
                    width: 100%;
                    height: 100vh;
                    font-size: 15px;
                }
            }
        }
    }

    .characteristics-footer {
        &__form {
            flex-wrap: wrap;
            align-items: center;
            justify-content: center;
        }

        &__item,
        &__btn {
            flex-basis: 70%;
            max-width: 70%;
            margin: 20px;
        }
    }

    .related-products {
        .related-products-box {
            &__item {
                flex-wrap: wrap;
                justify-content: center;
                margin-bottom: 50px;
            }

            &__img {
                margin-bottom: 20px;
            }
        }
    }
}

@media screen and (max-width: 500px) {
    .characteristics .characteristics-box,
    .description .description-box,
    .related-products .related-products-box,
    .video,
    .reviews,
    .results {
        &__title {
            font-size: 25px;
        }
    }

    .header,
    .footer {
        .ordering-info-1 {
            &__body-footer {
                display: flex;
                flex-direction: column;
                align-items: center;
                padding: 30px 0;
            }

            &__phone-footer {
                margin: 0;
            }

            &__title {
                font-size: 13px;
                text-align: center;
            }
        }

        .ordering-info-2 {
            &__price {
                left: 120px;
                width: 10%;
                min-width: 80px;
                height: 80px;
                min-height: 80px;

                p {
                    font-size: 14px;
                }

                b {
                    font-size: 16px;
                }
            }

            &__feature {
                bottom: 5px;
                font-size: 12px;

                div {
                    margin-left: 10px;
                }
            }

            &__content-box {
                font-size: 14px;
            }

            .ordering-info-2-counter {
                &__container {
                    padding-left: 0;
                    margin: 0 auto;
                }
            }
        }
    }

    .characteristics {
        .characteristics-box {
            .characteristics-slider-box {
                max-width: 600px;
                margin: 0 auto;

                .characteristics-slider {
                    &__text {
                        margin-right: 10px;
                        font-size: 14px;

                        span {
                            font-size: 18px;
                        }
                    }

                    &__discount {
                        padding: 20px;
                        font-size: 18px;
                    }
                }
            }

            .characteristics-text-info,
            .characteristics-slider-box {
                h3 {
                    font-size: 18px;
                }

                li {
                    font-size: 14px;
                }
            }
        }
    }

    .characteristics-footer {
        &__item,
        &__btn {
            flex-basis: 100%;
            max-width: 100%;
            margin: 20px;
        }
    }

    .description {
        .description-box {
            &__text {
                font-size: 14px;
            }
        }
    }

    .results {
        .results-box {

            &__item {
                display: flex;
                min-width: 300px;
                margin: 0 0 55px 0;

                img {
                    width: 290px;
                }
            }

            &__info {
                display: flex;
                align-items: center;
                width: 290px;
                margin: 0 auto;
            }

            &__info-text:last-child,
            &__info-text:first-child {
                width: 50%;
                height: 35px;
                padding: 5px 0 0 0;
            }
        }
    }

    .reviews {
        .reviews-box {
            &__item {
                font-size: 12px;
            }
        }
    }
}
