@mixin body {
    max-width: 960px;
    padding: 0 10px;
    margin: 0 auto;
}

@mixin btn-basis($bg-btn, $fz-btn:$fz-text-24px, $color-btn:$color-white) {
    font-size: $fz-btn;
    color: $color-btn;
    text-align: center;
    background: $bg-btn;
}

@mixin title-basis($fz-title-basis:$fz-title-24px, $color-title-basis:$color-gray-dark) {
    font-size: $fz-title-basis;
    color: $color-title-basis;
}

@mixin text-basis($fz-text-basis:$fz-text-18px, $color-text-basis:$color-gray-dark) {
    font-size: $fz-text-basis;
    color: $color-text-basis;
}

@mixin trans-basis($trans-basis:all) {
    transition: .3s linear $trans-basis;
}

@mixin section-info {
    h3 {
        margin: 10px 0 0;
        font-size: $fz-title-30px;
        color: $color-white;
        letter-spacing: 1.2px;
    }

    p {
        margin: 0;
        font-size: $fz-text-24px;
        color: $color-white;
    }

    svg {
        .a {
            fill: #fccd0f;
        }
    }
}

@mixin triangle-top {
    position: relative;

    &::before {
        position: absolute;
        top: -20px;
        right: 0;
        left: 0;
        box-sizing: border-box;
        width: 20px;
        height: 10px;
        margin: 0 auto;
        content: '';
        border: 10px solid transparent;
        border-bottom: 10px solid $bg-yellow-with-transparency;
    }
}
